import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { HttpTestService } from 'src/app/http-test.service';
import { Router } from '@angular/router';
import { constants } from 'src/constants/constants';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { ActionSheetController } from '@ionic/angular';
@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {
  public fGroup: FormGroup;
  submitted = false;
  header1: string;
  header2: string;
  header3: string;
  nome: string;
  birthUser:string;
  emailUser: string;
  profile_picture_url: string;
  constructor(private  router:  Router,private fBuilder: FormBuilder, private service: HttpTestService,private camera: Camera, public actionSheetCtrl: ActionSheetController) { }

  ionViewDidEnter(){
    this.header1 = window.sessionStorage.getItem('uid');
    this.header2 = window.sessionStorage.getItem('access-token');
    this.header3 = window.sessionStorage.getItem('client');
    this.service.getUserData('api/v1/users', this.header1, this.header2, this.header3).subscribe(resp =>{
      console.log("aqui esta o response" +JSON.stringify(resp.body));
      this.nome = resp.body.user.name;
      this.emailUser = resp.body.user.email;
      this.birthUser = resp.body.client_detail_attributes.birthdate
      //this.nome = this.nome.split(' ').length > 2 ? this.nome.split(' ').slice(0, -1).join(' '): this.nome;
      this.profile_picture_url = resp.body.user.image_url ? `${constants.baseUrlProd}${resp.body.user.image_url}`: "../../../assets/imgs/unknown_user.png"
                  
    });
  }
  ngOnInit() {
    this.fGroup = this.fBuilder.group({
      image:new FormControl('', Validators.required),
      name: new FormControl('', [Validators.required, Validators.minLength(3), Validators.pattern(/^[a-zA-Z\'\-\u00C0-\u00FF].*[\]+[a-zA-Z\'\-\u00C0-\u00FF].*$/)]),
      email: new FormControl('', [Validators.required, Validators.email]),
      password: new FormControl('', [Validators.required, Validators.minLength(6)]),
      password_confirmation: new FormControl('', Validators.required),
      birthdate: new FormControl('', Validators.required)
    }, {
      validators: this.password.bind(this)
    });
  }
  get f() { return this.fGroup.controls; }

  password(formGroup: FormGroup) {
    const { value: password } = formGroup.get('password');
    const { value: password_confirmation } = formGroup.get('password_confirmation');
    return password === password_confirmation ? null : { passwordNotMatch: true };
  }
  submitForm() {
    //debugger
    this.service.updateUser('api/v1/users', parseInt(window.sessionStorage.getItem('id')), this.fGroup, this.header1, this.header2, this.header3).subscribe(data => {
      console.log(data.body);
      console.log(data)
      if (data.status === 200 || data.status === 201){
      this.router.navigateByUrl('home');
      //  this.loading.dismiss();
      //  this.toast.present();
      }else {
      }
    }, error => {
      //console.log("teste valendo junior");
      // this.loading.dismiss();
      // this.toastFail.present();
    });
  }

  async changePicture() {
    const actionSheet = await this.actionSheetCtrl.create({
      header: 'Alterar foto do perfil',
      buttons: [
        {
          text: 'Tirar foto',
          handler: () => {
            this.getImageFromCamera();
          }
        },
        {
          text: 'Selecionar foto da galeria',
          handler: () => {
            this.getImageFromGallery();
          }
        }
        ,{
          text: 'Cancelar',
          role: 'cancel',
          handler: () => {}
        }
      ]
    });
    actionSheet.present();
  }

  private getImageFromGallery() {
    const options: CameraOptions = {
      quality: 70,
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      correctOrientation: true
    }
    this.camera.getPicture(options).then((imageData) => {
      // this.user.profile_picture_url = 'data:image/jpeg;base64,' + imageData;
      this.fGroup.value.image = 'data:image/jpeg;base64,' + imageData;
      this.profile_picture_url = this.fGroup.value.image
    }, (err) => { });
  }

  private getImageFromCamera() {
    const options: CameraOptions = {
      quality: 70,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      sourceType: this.camera.PictureSourceType.CAMERA,
      correctOrientation: true
    }
    
    this.camera.getPicture(options).then((imageData) => {
      // this.user.profile_picture_url = 'data:image/jpeg;base64,' + imageData;
      this.fGroup.value.image = 'data:image/jpeg;base64,' + imageData;
      this.profile_picture_url = this.fGroup.value.image
      //this.pictureChanged = true;
      //this.updatePicture();
    }, (err) => { 
      console.log(err);
    });
  }

  home(){
    this.router.navigateByUrl('home');
  }

  // async updatePicture(){
  //   await this.service.updateData('api/v1/users',parseInt(window.sessionStorage.getItem('id')),this.profile_picture_url, this.header1, this.header2, this.header3).subscribe(resp =>{
  //     if(resp.status === 200){
  //       confirm("Atualizado com sucesso.")
  //     }else{
  //       confirm("Erro ao atualizar. Tente novamente mais tarde.")
  //     }
  //    });  
  // }
}
